.btn {
  font-family: $font-main;
  display: block;
  color: $color-secondary;
  font-weight: 600;
  font-size: 1.2rem;
  font-variant: small-caps;
  letter-spacing: 0.07em;
  line-height: 1.5;
  background-color: $color-primary;
  padding: .7rem 4rem;
  border: none;

  &--large {
    padding: 1.5rem 11rem;
  }

  &--medium {
    padding: .9rem 11rem;
  }
}